import { Form, Button, Card, Row, Col, ListGroup } from "react-bootstrap";
import { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import { tableau } from "../../models/categorie.model.ts";
import FicheCategorie from "../../components/FicheCategorie";
import HeaderAdmin from "../../partials/HeaderAdmin";
import { useNavigate } from "react-router-dom";
import axios from "axios";

function Categories() {
  const [data, setData] = useState();
  const [detail, setDetail] = useState(null);
  const [isLoad, setLoad] = useState(false);
  const navigate = useNavigate();

  const nom = useRef();
  const description = useRef();

  const changeActu = (actu) => {
    setDetail(actu);
    //alert("mety");
  };

  const ajoutCategorie = () => {
    let token = sessionStorage.getItem("token");
    let categorie = {
      nom_categorie: nom.current.value,
      description: description.current.value,
    };
    axios
      .post(
        "http://webservicejo-production.up.railway.app/categories?hash=" +
          token,
        categorie
      )
      .then((response) => {
        console.log(response.data);
        setData((current) => [...current, response.data]);
      })
      .catch((error) => {
        //if (error.response.status === 401) {
        console.log(error.response.data);
        alert(JSON.stringify(error, null, 2));
        //}
      });
  };

  useEffect(() => {
    if (sessionStorage.getItem("token") == null) {
      navigate("/back-office");
    }
    let token = sessionStorage.getItem("token");
    axios
      .get(
        "http://webservicejo-production.up.railway.app/categories?hash=" + token
      )
      .then((response) => {
        //
        // alert(JSON.stringify(response.data, null, 2));
        setData(response.data);
        setLoad(true);
      })
      .catch((error) => {
        //if (error.response.status === 401) {
        console.log(error.response.data.message);
        alert(JSON.stringify(error.response.data.message, null, 2));
        //}
      });
  }, []);

  return !isLoad ? (
    "loading..."
  ) : (
    <div>
      <HeaderAdmin />
      <div className="container" style={{ marginTop: "20px" }}>
        <Card>
          <Card.Header>
            <Card.Title>Ajout categorie</Card.Title>
          </Card.Header>
          <Card.Body>
            <Row>
              <Col xs={6}>
                <Form>
                  <Form.Group>
                    <Form.Label>Nom</Form.Label>
                    <Form.Control type="text" ref={nom} required></Form.Control>
                  </Form.Group>
                  <Form.Group>
                    <Form.Label>Description</Form.Label>
                    <Form.Control
                      type="text"
                      ref={description}
                      required
                    ></Form.Control>
                  </Form.Group>
                  <Button
                    className="w-100 mt-2"
                    onClick={() => {
                      ajoutCategorie();
                    }}
                  >
                    Log In
                  </Button>
                </Form>
              </Col>
              <Col xs={6}>
                {/* <center> */}
                <h3>Liste des categorie existant(s)</h3>
                {/* </center> */}
                <div>
                  <ListGroup>
                    {data.map((categorie, index) => (
                      // isLoad ? <p>Mety</p> : <p>loading...</p>
                      // "mande " + categorie.nom_categorie
                      <FicheCategorie
                        data={categorie}
                        key={index}
                        changeActu={changeActu}
                      />
                    ))}
                  </ListGroup>
                </div>
              </Col>
            </Row>
          </Card.Body>
          <Card.Footer>
            {detail !== null ? (
              <div className="actu" style={{ textAlign: "center" }}>
                <Card.Subtitle>
                  <h3>{detail.nom_categorie}</h3>
                </Card.Subtitle>
                <Card.Text>Description : {detail.description}</Card.Text>
              </div>
            ) : (
              ""
            )}
          </Card.Footer>
        </Card>
        <br />
        <Link to={"/back-office"}>BackOffice</Link>
      </div>
    </div>
  );
}
export default Categories;
