import { useEffect, useState } from "react";
import { Button, Container, Nav, Navbar, NavDropdown } from "react-bootstrap";

function HeaderUser({ produits }) {
  const [isConnected, setConnect] = useState(false);

  function handleTeste() {
    //alert("cliqued");
    setConnect(!isConnected);
  }
  useEffect(() => {
    console.log("produitsss" + JSON.stringify(produits, null, 1));
  }, []);

  return (
    <Navbar bg="light" expand="lg">
      <Container>
        <Navbar.Brand href="/back-office">Auction's Page</Navbar.Brand>
        <div>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="/">Home</Nav.Link>
              <Nav.Link href="/historique">Historique</Nav.Link>
              <NavDropdown title="Categories" id="basic-nav-dropdown">
                {produits.map((p, index) => (
                  <NavDropdown.Item href="#" key={index}>
                    {p.nom_categorie}
                  </NavDropdown.Item>
                ))}
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </div>
        <Navbar.Collapse className="justify-content-end">
          <Navbar.Text>
            <Button
              onClick={() => {
                handleTeste();
              }}
            >
              teste
            </Button>
          </Navbar.Text>
          <Navbar.Text>
            {isConnected ? (
              <a href="/logIn">LogIn?</a>
            ) : (
              <a href="#login">LogOut?</a>
            )}
          </Navbar.Text>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}
export default HeaderUser;
