import { Form, Button, Card } from "react-bootstrap";
import { useRef } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { useNavigate } from "react-router-dom";

function LogIn() {
  const username = useRef();
  const password = useRef();
  const navigate = useNavigate();

  const handleSubmit = () => {
    let admin = {
      userName: username.current.value,
      passWord: password.current.value,
    };
    //alert(JSON.stringify(admin, null, 2));
    axios
      .post("https://webservicejo-production.up.railway.app/admins", admin)
      .then((response) => {
        sessionStorage.setItem("token", response.data.data[1].value);
        navigate("./categories");
      })
      .catch((error) => {
        //if (error.response.status === 401) {
        console.log(error.response.data.message);
        console.log(JSON.stringify(error));
        alert(JSON.stringify(error.response.data.message, null, 2));
        //}
      });
  };
  return (
    <div className="container">
      <Card>
        <Card.Header>
          <Card.Title>Log Ins</Card.Title>
        </Card.Header>
        <Card.Body>
          <Form>
            <Form.Group>
              <Form.Label>Identifiant</Form.Label>
              <Form.Control
                type="username"
                ref={username}
                defaultValue="admin"
                required
              ></Form.Control>
            </Form.Group>
            <Form.Group>
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                ref={password}
                defaultValue="admin"
                required
              ></Form.Control>
            </Form.Group>
            <Button
              className="w-100 mt-2"
              onClick={() => {
                handleSubmit();
              }}
            >
              Log In
            </Button>
          </Form>
          <Link to={"/"}>logOut</Link>
          <br />
          <Link to={"./categories"}>categories</Link>
        </Card.Body>
      </Card>
    </div>
  );
}
export default LogIn;
