import axios from "axios";

export class Categorie {
  constructor(
    public id: number,
    public nom_categorie: string,
    public description: string,
    public duree: number
  ) {}
}

export const tableau = () => {
  return [
    new Categorie(1, "categorie1", "description1", 20),
    new Categorie(2, "categorie2", "description2", 10),
    new Categorie(3, "categorie3", "description3", 30),
    new Categorie(1, "categorie4", "description4", 20),
    new Categorie(2, "categorie5", "description5", 30),
  ];
};

export const getStat = async () => {
  let token = sessionStorage.getItem("token");
  let res;
  axios
    .get("http://192.168.119.42:8080/statistiques?hash=" + token)
    .then((result) => {
      console.log(result);
      res = result.data.data;
      console.log("res = " + JSON.stringify(res, null, 2));
    })
    .catch((error) => {
      alert(error.response.data.message);
    });
  return res;
};
