import FicheCategorie from "../FicheCategorie";
import { Link } from "react-router-dom";

function FicheRecharge({ data, valider, index }) {
  return (
    <tr>
      <td>{data.compte.utilisateur.nom}</td>
      <td>{data.compte.utilisateur.prenom}</td>
      <td>{data.valeur_recharge}</td>
      <td>
        <Link
          onClick={() => {
            valider(data.id, index);
          }}
        >
          Valider
        </Link>
      </td>
    </tr>
  );
}
export default FicheRecharge;
