import HeaderAdmin from "../../partials/HeaderAdmin";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar } from "react-chartjs-2";
import { Col, Row, Table } from "react-bootstrap";
import { getStat } from "../../models/categorie.model.ts";
import { useEffect, useState } from "react";
import axios from "axios";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

// const datas = {
//   labels: ["vetement", "vehicule", "loisir", "adulte", "collection"],
//   datasets: [
//     {
//       label: "statistique par rapport aux categorie",
//       data: ["25", "12", "45", "5", "30"],
//       backgroundColor: "rgba(255, 99, 132, 0.5)",
//     },
//   ],
// };

function Statistique() {
  const [data, setData] = useState();
  const [isLoad, setLoad] = useState(false);
  const [config, setConfig] = useState();
  const [liste, setListe] = useState();

  useEffect(() => {
    let token = sessionStorage.getItem("token");
    const initial = async () => {
      const fetchData = await axios.get(
        "http://webservicejo-production.up.railway.app/statistiques?hash=" +
          token
      );
      setData(fetchData.data.data);
      console.log(JSON.stringify(fetchData.data.data, null, 2));

      const liste = fetchData.data.data.nom_categorie.map((item1, index) => [
        item1,
        fetchData.data.data.prix_moyenne[index],
      ]);
      setListe(liste);

      const da = {
        labels: fetchData.data.data.nom_categorie,
        datasets: [
          {
            label: "statistique par rapport aux categorie",
            data: fetchData.data.data.prix_moyenne,
            backgroundColor: "rgba(255, 99, 132, 0.5)",
          },
        ],
      };
      setConfig(da);
      setLoad(true);
    };
    initial();
  }, []);

  return !isLoad ? (
    "loading..."
  ) : (
    <div>
      <HeaderAdmin />
      <div className="container" style={{ marginTop: "20px" }}>
        <Row>
          <Col xs={6}>
            <h3>Statiques</h3>
            <div>
              <Bar data={config} />
            </div>
          </Col>
          <Col xs={6}>
            <h3>Statiques(tableau)</h3>
            <Table striped>
              <thead>
                <tr>
                  <th>Categorie</th>
                  <th>Prix moyenne(vendu) par categorie</th>
                </tr>
              </thead>
              <tbody>
                {liste.map(([data1, data2], index) => (
                  <tr key={index}>
                    <td>{data1}</td>
                    <th>{data2}</th>
                  </tr>
                ))}
              </tbody>
            </Table>
          </Col>
        </Row>
      </div>
    </div>
  );
}

export default Statistique;
