import HeaderAdmin from "../../partials/HeaderAdmin";
import { Table } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import axios from "axios";
import FicheRecharge from "../../components/admin/FicheRecharge";

function DemandeRecharge() {
  const [data, setData] = useState();
  const [isLoad, setLoad] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    if (sessionStorage.getItem("token") == null) {
      navigate("/back-office");
    }
    let token = sessionStorage.getItem("token");
    axios
      .get(
        "http://webservicejo-production.up.railway.app/recharges?hash=" + token
      )
      .then((response) => {
        // console.log(response.data.data);
        // console.log(response.data.data[0]);
        // console.log(response.data.data[0].compte.utilisateur);
        setData(response.data.data);
        setLoad(true);
      })
      .catch((error) => {
        //if (error.response.status === 401) {
        console.log(error);
        console.log(error.response.data);
        alert(JSON.stringify(error, null, 2));
        //}
      });
  }, []);

  const valider = (id, numero) => {
    let token = sessionStorage.getItem("token");
    let recharge = {
      id: id,
    };
    alert("idd = " + id);
    axios
      .post(
        "http://webservicejo-production.up.railway.app/recharges?hash=" + token,
        recharge
      )
      .then((response) => {
        navigate("/back-office/categories");
      })
      .catch((error) => {
        //if (error.response.status === 401) {
        console.log(error.response.data);
        alert(JSON.stringify(error, null, 2));
        //}
      });
  };

  return !isLoad ? (
    "loading ..."
  ) : (
    <div>
      <HeaderAdmin />
      <div className="container" style={{ marginTop: "20px" }}>
        <center>
          <h1>Demande recharge</h1>
        </center>
        <div style={{ marginTop: "10px" }}>
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>First Name</th>
                <th>Last Name</th>
                <th>Somme</th>
                <td>Action</td>
              </tr>
            </thead>
            <tbody>
              {data.map((recharge, index) => (
                <FicheRecharge
                  data={recharge}
                  valider={valider}
                  key={index}
                  index={index}
                />
              ))}
            </tbody>
          </Table>
        </div>
      </div>
    </div>
  );
}
export default DemandeRecharge;
