import { useState } from "react";
import { Navbar, Nav, Container } from "react-bootstrap";
import { BrowserRouter, Link, Navigate, NavLink } from "react-router-dom";

function HeaderAdmin() {
  const [islogOut, setLogOut] = useState(false);

  const logOut = () => {
    sessionStorage.removeItem("token");
    setLogOut(true);
  };
  return islogOut ? (
    <Navigate to="/" />
  ) : (
    <Navbar bg="dark" variant="dark">
      <Container>
        <Navbar.Brand>Back-office</Navbar.Brand>
        <Nav className="me-auto">
          <NavLink to={"/back-office/categories"} className="nav-link">
            Liste Categorie
          </NavLink>
          <NavLink to={"/back-office/recharges"} className="nav-link">
            Demande recharge
          </NavLink>
          <NavLink to={"/back-office/statistiques"} className="nav-link">
            Statistique
          </NavLink>
        </Nav>
        <Navbar.Collapse className="justify-content-end">
          <Navbar.Text>
            <Link
              onClick={() => {
                logOut();
              }}
            >
              Log out?
            </Link>
          </Navbar.Text>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}
export default HeaderAdmin;
